@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 5rem;
    width: 100%;

    p{
        font-size: 5rem;
    }
    @include m.tablet{
        //  display: flex;
        // justify-content: space-between;
        // display: grid;
        // grid-template-columns: repeat(2, 1fr);
    }
}

.campo{

    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label {
        font-size: 2rem;
        font-weight: 600;
        flex: 0 0 10rem;
    }
    
    input {    
        width:30rem;
        height: 5rem; 
        @include m.telefono{
            border: none; //le quita el borde a los inputs
            padding: calc(v.$separacion / 4);  //calc es una funcion que me permite hacer operaciones en el css
            border-radius: 1rem;  // 1 rem equivale a 10 px
            width:50rem;
            height: 5rem;
            margin-right: 2rem;
            font-size: 2rem;
        }

    }

    @include m.tablet {
        display:block;    
        }

}

.campo-detalle_producto{

    background-color: v.$grisDark;
    border-radius: 1rem;
    color: #fff;    
    font-weight: 800;
    margin-bottom: 1rem;
    padding: 1rem;
    width: 40%;
    text-align: center;
}

.campo-selector_producto{
    background-color: #8498c2;
    border-radius: 1rem;
    color: #fff;    
    font-weight: 800;
    margin-bottom: 1rem;
    padding: 1rem;
    width: 20%;
    text-align: center;  

}

.campo-detalle{

    background-color: v.$grisDark;
    border-radius: 1rem;
    color: #fff;    
    font-weight: 800;
    margin-bottom: 1rem;
    padding: 1rem;
    width: 50rem;
    height: 20rem;
    text-align: justify;
}

.campo-selector {
    // background-color: #2b4986;
    background-color: v.$azulOscuro;
    border-radius: 1rem;
    color: #fff;    
    font-weight: 800;
    margin-bottom: 1rem;
    padding: 1rem;
    text-transform: uppercase;
    transition: background-color .3s ease;
    width: 30%;
    text-align: center;
}

.selector-ancho_upload{
    background-color: v.$azulOscuro;
    border-radius: 1rem;
    color: #fff;    
    font-weight: 800;
    margin-bottom: 1rem;
    padding: 1rem;    
    width: 70%;
}